// =============================================================================
// Reset (reset wordpress default styles inside wpfs)
// =============================================================================

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="range"],
input[type="date"],
input[type="month"],
input[type="week"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[type="color"],
textarea {
  &.#{$wpfs}-form-control {
    background: var(--form-control-bg-color);
    background-image: none;
    display: inline-block;
    width: 100%;
    height: 40px;
    color: var(--primary-color);
    font-family: var(--font-family);
    font-size: 15px;
    padding: 0 spacer(2);
    border: 1px solid var(--form-control-border-color);
    border-radius: var(--default-border-radius);
    transition: border-color .3s ease, box-shadow .3s ease;
    float: none;
    margin-bottom: 0;
    margin-left: 0;
    margin-right: 0;
    letter-spacing: normal;
    font-weight: 400;

    &:focus {
      outline: none;
    }
  }

  &.#{$wpfs}-input-group-form-control {
    &:focus {
      outline: none;
    }
  }

  &.#{$wpfs}-form-control--error {
    border-color: var(--error-color);
  }
}

[class^="#{$wpfs}-"],
[class*=" #{$wpfs}-"] {
  line-height: normal;
}

.#{$wpfs} {
  $root: &;

  &-input-group {
    & > #{$root}-input-group-form-control {
      background: var(--form-control-bg-color);
      background-image: none;
      color: var(--primary-color);
      display: inline-block;
      padding: 0 spacer(2);
      border: 0;
      border-radius: var(--default-border-radius);
      font-size: 15px;
      height: 40px;
      margin: 0;
      float: none;
    }
  }

  &-form-group,
  &-form-check-group {
    text-align: left;
  }

  &-form-label,
  &-form-check-group-title {
    float: none;
    padding: 0;
    width: auto;
    text-align: left;
  }

  &-form-check-label {
    display: inline;
    float: none;
    font-weight: normal;
    padding: 0 0 0 24px;
  }

  &-btn {
    text-transform: none;
    margin-bottom: 0;
  }

  &-btn-primary {
    &:focus,
    &:not(.wpfs-btn-primary--disabled):not(:disabled):not(:active):hover {
      color: var(--primary-font-color);
    }
  }

  &-btn-outline-primary {
    &:focus,
    &:not(#{&}--disabled):not(#{&}--selected):not(:disabled):hover {
      background: var(--outline-primary-bg-color);
    }

    &--selected {
      &:hover {
        background: var(--primary-bg-color);
      }
    }

    &:disabled,
    &-disabled {
      background: transparent;
    }
  }

  &-btn-link {
    font-weight: normal;
    background: transparent;
    border-radius: 0;

    &--disabled,
    &:disabled {
      background: transparent;
      color: var(--link-disabled-font-color);

      &:hover {
        color: var(--link-disabled-font-color);
      }
    }
  }

  &-datepicker-div {
    .ui-datepicker-calendar {
      margin: 0;

      tr {
        border: 0;
      }

      th {
        padding: 0;
        border: 0;
        text-align: center;
      }
    }
  }

  &-button-group {
    #{$root}-form-check-input {
      &:checked + #{$root}-btn-outline-primary:hover {
        background: var(--primary-bg-color);
      }
    }
  }

  &-summary-table {
    min-width: auto;
    border: 0;
    margin: 0 !important;

    tbody {
      background: none !important;
      border: 0 !important;
    }

    tfoot {
      background: none !important;
      color: var(--primary-color) !important;
      border: 0 !important;
    }
  }

  &-summary-table-row,
  &-summary-table-total {
    background: none !important;
    border: 0 !important;
  }

  &-summary-table-cell {
    border: 0;

    &:first-child {
      font-weight: normal !important;
    }

    &:last-child {
      text-align: right !important;
    }

    &:first-child,
    &:last-child {
      padding: spacer(0.5) !important;
    }
  }

  &-tooltip {
    padding-top: 0;
    padding-bottom: 0;
    margin-bottom: 0;
  }

  &-form-check-group-title {
    border: 0;
  }

  &-form {
    .select2 {
      display: none;
    }
  }

  &-form-row {
    #{$root}-form-group {
      margin-bottom: spacer(3);
    }
  }

  &-selectmenu-button .ui-icon {
    display: block !important;
  }
}

.entry-content {
  .#{$wpfs} {
    &-input-group-link {
      box-shadow: none;

      &:hover,
      &:focus {
        color: var(--link-hover-font-color);
        box-shadow: none;
      }
    }

    &-btn {
      text-transform: none;
    }

    &-btn-link {
      box-shadow: none;

      &:not(.#{$wpfs}-btn-link--disabled):not(:disabled):focus,
      &:not(.#{$wpfs}-btn-link--disabled):not(:disabled):hover {
        color: var(--link-hover-font-color);
        box-shadow: none;
      }

      &--disabled,
      &:disabled {
        &:focus,
        &:hover {
          color: var(--link-disabled-font-color);
          box-shadow: none;
        }
      }
    }

    &-stepper .ui-spinner-button {
      font-size: 16px;
      box-shadow: none;
      text-decoration: none;
    }

    &-form-label a,
    &-form-check-label a {
      color: var(--link-font-color);
      box-shadow: none;
      text-decoration: none;
    }
  }

  input[type="text"],
  input[type="email"],
  input[type="url"],
  input[type="password"],
  input[type="search"],
  input[type="number"],
  input[type="tel"],
  input[type="range"],
  input[type="date"],
  input[type="month"],
  input[type="week"],
  input[type="time"],
  input[type="datetime"],
  input[type="datetime-local"],
  input[type="color"],
  textarea {
    &.#{$wpfs}-form-control,
    &.#{$wpfs}-input-group-form-control {
      &:focus {
        outline: none;
        box-shadow: none;
      }
    }
  }
}

.entry {
  .entry-content {
    .#{$wpfs} {
      &-input-group-link {
        text-decoration: none;
      }

      &-btn {
        text-transform: none;
      }

      &-btn-link {
        text-decoration: none;
      }
    }
  }
}

input[type="radio"] + .#{$wpfs}-btn-outline-primary {
  font-weight: bold;
}

button.wpfs-btn-primary[type=submit],
.no-touch button.wpfs-btn-primary[type=submit] {
  @extend .wpfs-btn-primary;
}

#content .wpb_text_column {
  .#{$wpfs} {
    &-form-check {
      padding: 0;
    }

    &-form-check-group {
      .#{$wpfs}-form-check {
        padding: 0;
        margin-bottom: spacer(2);
      }
    }

    &-form-row {
      .#{$wpfs}-form-group {
        margin-bottom: spacer(3);
      }
    }
  }

  input[type=checkbox]+label,
  input[type=radio]+label {
    margin: 0;
  }
}


.#{$wpfs}-form-select {
  .stm_select {
    display: none;
  }
}

input[type="checkbox"].#{$wpfs}-form-check-input + .#{$wpfs}-form-check-label,
input[type="radio"].#{$wpfs}-form-check-input + .#{$wpfs}-form-check-label {
  font-size: 15px;
  padding-left: 24px;
  margin-left: 0;
  margin-bottom: 0;
  vertical-align: baseline;
}

input[type="checkbox"].#{$wpfs}-form-check-input + .#{$wpfs}-btn,
input[type="radio"].#{$wpfs}-form-check-input + .#{$wpfs}-btn {
  font-size: 15px;
  margin-left: 0;
  margin-bottom: 0;
  vertical-align: baseline;
}

.#{$wpfs}-stepper {
  .ui-spinner {
    background: var(--form-control-bg-color);
    border: 0;
    overflow: visible;

    a.ui-spinner-button {
      background: none;
      border: 0;
      text-decoration: none;

      .ui-button-text {
        display: block;
        padding: 0;
      }

      .ui-icon {
        background: none;
        text-indent: 0;
        display: block;
        position: static;
        text-decoration: none;
        transition: color .3s ease;
        font-size: 18px;
      }
    }
  }

  .#{$wpfs}-form-control {
    margin: 0;
  }
}
