// =============================================================================
// Combobox
// =============================================================================

.#{$wpfs} {
  $root: &;

  &-combobox {
    #{$root}-input-group-icon {
      font-size: 16px;
      cursor: pointer;
      height: 16px;
    }

    #{$root}-icon-arrow {
      display: inline-block;
      transition: transform .3s ease;
    }

    #{$root}-input-group-form-control {
      color: var(--primary-color) !important;
    }

    &--open {
      #{$root}-icon-arrow {
        transform: rotate(180deg);
      }
    }
  }

  &-combobox-menu {
    &.ui-menu {
      position: absolute;
      top: 0;
      left: 0;
      max-height: 240px;
      overflow: auto;
      overflow-x: hidden;
    }

    .ui-menu-item {
      &.ui-state-disabled {
        cursor: not-allowed;
      }

      &:not(.ui-state-disabled) {
        &.ui-state-focus {
          .ui-menu-item-wrapper:not(.ui-state-selected) {
            background: var(--ui-state-active-bg-color);
          }
        }

        .ui-state-active {
          background: var(--ui-state-active-bg-color);
        }

        .ui-state-selected {
          background: var(--ui-state-selected-bg-color);
          color: var(--ui-state-selected-font-color);
        }
      }
    }

    .ui-menu-item-wrapper {
      font-size: 15px;
      padding: spacer(1) 0 spacer(1) spacer(2);
      transition: background .3s ease;
    }
  }
}
