// =============================================================================
// Selectmenu
// =============================================================================

.#{$wpfs} {
  $root: &;

  &-selectmenu {
    &-button {
      width: 100% !important;
      white-space: nowrap;

      &:hover {
        background: #fff;

        .ui-selectmenu-text {
          color: var(--primary-color);
        }
      }

      &.ui-selectmenu-button-open {
        .ui-icon {
          transform: rotate(180deg);
        }
      }

      &.ui-state-placeholder {
        .ui-selectmenu-text {
          color: var(--form-base-color);
        }
      }

      .ui-icon {
        position: absolute;
        top: 50%;
        right: 16px;
        width: 16px;
        height: 16px;
        color: var(--form-base-color);
        font-size: 16px;
        transition: transform .3s ease;
        pointer-events: none;
        margin-top: -8px;
      }

      .ui-selectmenu-text {
        display: block;
        overflow: hidden;
        text-overflow: ellipsis;
        padding: 10px 24px 10px 0;
        transition: color .3s ease;
      }
    }

    &-menu {
      display: none;
      position: absolute;
      top: 0;
      left: 0;
      padding: 4px 0 0 0;
      margin: 0;

      &.ui-selectmenu-open {
        display: block;
      }

      .ui-menu {
        max-height: 240px;
        overflow: auto;
        overflow-x: hidden;
      }

      .ui-menu-item:not(.ui-state-disabled) {
        &.ui-state-focus {
          .ui-menu-item-wrapper:not(.ui-state-selected) {
            background: var(--ui-state-active-bg-color);
          }
        }
      }

      .ui-menu-item-wrapper {
        font-size: 15px;
        padding: 8px 0 8px 16px;
        transition: background .3s ease;
      }

      .ui-state-active {
        background: var(--ui-state-active-bg-color);
      }

      .ui-state-selected {
        background: var(--ui-state-selected-bg-color);
        color: var(--ui-state-selected-font-color);
      }

      .ui-selectmenu-optgroup {
        color: var(--form-base-color);
        font-size: 13px;
        font-weight: bold;
        padding: 9px 0 9px 16px;
        cursor: not-allowed;
        user-select: none;
        line-height: normal;
        margin: 0;
      }

      .ui-state-disabled {
        color: var(--form-base-color);
        cursor: not-allowed;
        user-select: none;
        opacity: 1;
        border: 0;

        &.ui-state-focus {
          color: var(--form-base-color);
        }

        .ui-state-active,
        .ui-menu-item-wrapper {
          background: #fff;
          outline: none;
        }
      }

      .ui-state-focus {
        border: 0;
        background: none;
        color: var(--primary-color);
      }
    }
  }
}
