// =============================================================================
// Buttons
// =============================================================================

.#{$wpfs} {
  &-btn {
    display: inline-block;
    cursor: pointer;
    padding: 0;
    border: 0;
    text-align: center;
    text-decoration: none !important;
  }

  &-btn-primary {
    background: var(--primary-bg-color);
    height: 40px;
    line-height: 38px;
    color: var(--primary-font-color);
    font-size: 17px;
    font-weight: bold;
    padding: 0 spacer(2);
    box-shadow: 0px 2px 2px rgba(47, 47, 55, .25);
    border-radius: var(--default-border-radius);
    transition: background-color .3s ease;
    text-transform: none;

    &:focus,
    &:not(#{&}--disabled):not(:disabled):not(:active):hover {
      background: var(--primary-hover-focus-bg-color);
      box-shadow: 0px 2px 2px rgba(47, 47, 55, .25);
      text-transform: none;
    }

    &:focus {
      outline: none;
    }

    &:active {
      background: var(--primary-active-bg-color);
      box-shadow: inset 0px 2px 2px rgba(47, 47, 55, .25);
      text-transform: none;
    }

    &:disabled,
    &--disabled,
    &--disabled:visited {
      background: var(--primary-disabled-bg-color);
      color: var(--primary-disabled-font-color);
      box-shadow: none;
      cursor: not-allowed;
      text-transform: none;
    }

    &--loader {
      position: relative;
      padding-left: 56px !important;
      text-transform: none;

      &:disabled,
      &--disabled {
        color: var(--primary-disabled-font-color);
      }

      &:after {
        @include base-icon;
        content: $wpfs-icon-spinner;
        position: absolute;
        top: 50%;
        left: 16px;
        font-size: 24px;
        margin-top: -12px;
        transform-origin: 50% 52%;
        animation: loader 1s infinite ease;
      }
    }
  }

  &-btn-danger {
    background: var(--danger-bg-color);
    height: 40px;
    line-height: 38px;
    color: var(--danger-font-color);
    font-size: 17px;
    font-weight: bold;
    padding: 0 spacer(2);
    box-shadow: 0px 2px 2px rgba(47, 47, 55, .25);
    border-radius: var(--default-border-radius);
    transition: background-color .3s ease;
    text-transform: none;

    &:focus,
    &:not(#{&}--disabled):not(:disabled):not(:active):hover {
      background: var(--danger-hover-focus-bg-color);
      box-shadow: 0px 2px 2px rgba(47, 47, 55, .25);
      text-transform: none;
    }

    &:focus {
      outline: none;
    }

    &:active {
      background: var(--danger-active-bg-color);
      box-shadow: inset 0px 2px 2px rgba(47, 47, 55, .25);
      text-transform: none;
    }

    &:disabled,
    &--disabled,
    &--disabled:visited {
      background: var(--danger-disabled-bg-color);
      color: var(--danger-disabled-font-color);
      box-shadow: none;
      cursor: not-allowed;
      text-transform: none;
    }

    &--loader {
      position: relative;
      padding-left: 56px;
      text-transform: none;

      &:disabled,
      &--disabled {
        color: #fff;
      }

      &:after {
        @include base-icon;
        content: $wpfs-icon-spinner;
        position: absolute;
        top: 50%;
        left: 16px;
        font-size: 24px;
        margin-top: -12px;
        transform-origin: 50% 52%;
        animation: loader 1s infinite ease;
      }
    }
  }

  &-btn-outline-primary {
    background: var(--outline-primary-bg-color);
    height: 40px;
    line-height: 38px;
    font-size: 15px;
    font-weight: bold;
    padding: 0 spacer(3);
    border: 1px solid var(--outline-primary-border-color);
    box-sizing: border-box;
    box-shadow: 0px 2px 2px rgba(47, 47, 55, .25);
    border-radius: var(--default-border-radius);
    transition: border-color .3s ease, background-color .3s ease, color .3s ease;
    text-transform: none;

    &:focus,
    &:not(#{&}--disabled):not(:disabled):hover {
      border-color: var(--outline-primary-hover-focus-border-color);
      text-transform: none;
    }

    &:focus {
      outline: none;
    }

    &:active {
      box-shadow: inset 0px 2px 2px rgba(47, 47, 55, .25);
      text-transform: none;
    }

    &:disabled,
    &--disabled,
    &--disabled:visited {
      background: var(--outline-primary-disabled-border-color);
      border-color: var(--outline-primary-disabled-border-color);
      color: var(--outline-primary-disabled-font-color);
      cursor: not-allowed;
      text-transform: none;
    }

    &--selected,
    &--selected:focus {
      background: var(--primary-bg-color);
      color: var(--outline-primary-selected-font-color);
      border-color: var(--primary-bg-color);
    }
  }

  &-btn-link {
    color: var(--link-font-color);
    font-size: 15px;
    text-decoration: none;
    transition: all .3s ease;
    border-bottom: 1px solid transparent;
    text-transform: none;

    &:visited {
      color: var(--link-font-color);
    }

    &:not(#{&}--disabled):not(:disabled):focus,
    &:not(#{&}--disabled):not(:disabled):hover {
      background: none;
      color: var(--link-hover-font-color);
      border-bottom-color: var(--link-hover-font-color);
      text-transform: none;
    }

    &:active {
      color: var(--link-active-font-color);
      border-bottom-color: var(--link-hover-font-color);
      text-transform: none;
    }

    &--disabled,
    &:disabled,
    &--disabled:visited {
      color: var(--link-disabled-font-color);
      cursor: not-allowed;
      text-transform: none;
    }

    &--bold {
      font-weight: bold;
      border-bottom: none;
    }

    &--sm {
      font-size: 13px;
    }

    &--loader {
      position: relative;
      padding-left: 30px;
      text-transform: none;

      &:after {
        @include base-icon;
        content: $wpfs-icon-spinner;
        position: absolute;
        top: 50%;
        left: 0;
        font-size: 20px;
        margin-top: -10px;
        transform-origin: 50% 55%;
        animation: loader 1s infinite ease;
      }
    }
  }
}

@keyframes loader {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
