// =============================================================================
// Tooltip
// =============================================================================

.#{$wpfs} {
  &-tooltip {
    position: absolute;
    max-width: 300px;
    z-index: 100;

    &.wpfs-ui.ui-widget-content {
      padding: 0;
      border: none;
    }
  }

  &-tooltip-content {
    display: none !important;
  }
}
