// =============================================================================
// Base
// =============================================================================

[class^="#{$wpfs}-"],
[class*=" #{$wpfs}-"] {
  font-family: var(--font-family);
  font-size: initial;
  color: var(--primary-color);
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
