// =============================================================================
// Coupon
// =============================================================================

.#{$wpfs} {
  &-coupon {
    position: relative;
    font-size: 16px;
    padding-left: spacer(3);
    margin-bottom: spacer(1);

    &:last-of-type {
      margin-bottom: 0;
    }

    &:before {
      @include base-icon;
      content: $wpfs-icon-check-circle;
      position: absolute;
      top: 1px;
      left: 0;
      font-size: 16px;
      font-weight: bold;
      color: var(--success-color);
    }
  }

  &-coupon-summary {
    font-size: 13px;
  }

  &-coupon-summary-note {
    color: var(--form-base-color);
  }
}
