// =============================================================================
// Summary
// =============================================================================

.#{$wpfs} {
  $root: &;

  &-tooltip {
    &.top {
      #{$root}-info-tooltip {
        &:after {
          bottom: 100%;
        }

        &:after {
          border-bottom-color: var(--primary-color);
        }
      }
    }

    &.bottom {
      #{$root}-info-tooltip {
        &:after {
          top: 100%;
        }

        &:after {
          border-top-color: var(--primary-color);
        }
      }
    }
  }

  &-info-tooltip {
    background: var(--primary-color);
    max-width: 240px;
    color: var(--ui-info-tooltip-font-color);
    font-size: 13px;
    font-weight: bold;
    line-height: 16px;
    padding: spacer(1);
    border-radius: var(--default-border-radius);
    margin-left: -10px;

    &:after {
      left: 6px;
      border: solid transparent;
      content: " ";
      height: 0;
      width: 0;
      position: absolute;
      pointer-events: none;
    }

    &:after {
      border-width: 6px;
      margin-left: -6px;
    }
  }
}
