// =============================================================================
// Icons
// =============================================================================

@font-face {
  font-family: 'wpfs-icons';
  src:
    url('../fonts/wpfs-icons/wpfs-icons.ttf') format('truetype'),
    url('../fonts/wpfs-icons/wpfs-icons.woff') format('woff'),
    url('../fonts/wpfs-icons/wpfs-icons.svg#wpfs-icons') format('svg');
  font-weight: normal;
  font-style: normal;
}

@mixin base-icon {
  font-family: 'wpfs-icons' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^="#{$wpfs}-icon-"],
[class*=" #{$wpfs}-icon-"] {
  @include base-icon;
}

.#{$wpfs} {
  &-icon-check-circle {
    &:before {
      content: $wpfs-icon-check-circle;
    }
  }

  &-icon-arrow {
    &:before {
      content: $wpfs-icon-arrow;
    }
  }

  &-icon-calendar {
    &:before {
      content: $wpfs-icon-calendar;
    }
  }

  &-icon-check {
    &:before {
      content: $wpfs-icon-check;
    }
  }

  &-icon-decrease {
    &:before {
      content: $wpfs-icon-decrease;
    }
  }

  &-icon-error-diamond {
    &:before {
      content: $wpfs-icon-error-diamond;
    }
  }

  &-icon-help-circle {
    &:before {
      content: $wpfs-icon-help-circle;
    }
  }

  &-icon-increase {
    &:before {
      content: $wpfs-icon-increase;
    }
  }

  &-icon-spinner {
    &:before {
      content: $wpfs-icon-spinner;
    }
  }

  &-icon-success-circle {
    &:before {
      content: $wpfs-icon-success-circle;
    }
  }

  &-icon-accounting-document {
    &:before {
      content: $wpfs-icon-accounting-document;
    }
  }
}
