// =============================================================================
// Media
// =============================================================================

$sm: 576px;
$md: 768px;
$lg: 992px;
$xl: 1200px;

$smm: 575.98px;
$mdm: 767.98px;
$lgm: 991.98px;
$xlm: 1199.98px;

// Breakpoint only
@mixin bp-only($point) {
	@if $point == xs {
		@media (max-width: $smm) {
			@content;
		}
	}
	@else if $point == sm {
		@media (min-width: $sm) and (max-width: $mdm) {
			@content;
		}
	}
	@else if $point == md {
		@media (min-width: $md) and (max-width: $lgm) {
			@content;
		}
	}
	@else if $point == lg {
		@media (min-width: $lg) and (max-width: $xlm) {
			@content;
		}
	}
	@else if $point == xl {
		@media (min-width: $xl) {
			@content;
		}
	}
}

// Breakpoint down
@mixin bp-down($point) {
	@if $point == xs {
		@media (max-width: $smm) {
			@content;
		}
	}
	@else if $point == sm {
		@media (max-width: $mdm) {
			@content;
		}
	}
	@else if $point == md {
		@media (max-width: $lgm) {
			@content;
		}
	}
	@else if $point == lg {
		@media (max-width: $xlm) {
			@content;
		}
	}
}
