// =============================================================================
// Forms
// =============================================================================

.#{$wpfs} {
  $root: &;

  &-form {
    width: 100%;
    text-align: left;

    &--inline {
      width: auto;
      display: inline-block;
    }
  }

  &-form-group {
    margin-bottom: spacer(3);
  }

  &-form-label,
  &-form-check-group-title {
    display: block;
    font-weight: bold;
    font-size: 13px;
    color: var(--form-base-color);

    &--with-info {
      position: relative;
      display: inline-block;
    }
  }

  &-form-label {
    margin-bottom: spacer(1);
  }

  &-form-check-group-title {
    margin-bottom: spacer(1.5);
  }

  &-form-check-group {
    background: none;
    display: block;
    padding: 0;
    margin: 0 0 spacer(1);
    border: none;

    #{$root}-form-check {
      margin-bottom: spacer(2);
    }
  }

  &-form-control {
    background: var(--form-control-bg-color);
    display: inline-block;
    width: 100%;
    height: 40px;
    font-size: 15px;
    padding: 0 spacer(2);
    border: 1px solid var(--form-control-border-color);
    border-radius: var(--default-border-radius);
    transition: border-color .3s ease, box-shadow .3s ease;

    &::placeholder {
      color: var(--form-control-placeholder-color);
      font-family: var(--font-family);
      transition: color .3s ease;
    }

    &:not(:disabled):not(#{&}--error):not(#{&}--focus):not(:focus):hover {
      border: 1px solid var(--form-control-hover-border-color);
    }

    &:focus {
      outline: none;
    }

    &:not(#{&}--error)#{&}--focus,
    &:not(#{&}--error):focus {
      border-color: var(--secondary-color);
      box-shadow: 0px 0px 0px 1px var(--secondary-color) !important;
    }

    &--error {
      border-color: var(--error-color);
    }
  }

  &-form-card {
    padding-top: 11px;

    .ElementsApp,
    .ElementsApp .InputElement {
      color: var(--primary-color);
      font-family: var(--font-family);
      font-size: '15px';

      &::placeholder {
        color: var(--form-control-placeholder-color);
        font-family: var(--font-family);
      }
    }
  }

  &-form-check {
    position: relative;
    margin-bottom: spacer(3);
  }

  &-form-check-input {
    &[type="checkbox"],
    &[type="radio"] {
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
      margin: 0;
      z-index: -1;
      width: auto;
      min-width: auto;
      height: auto;
      background: transparent;
      border: 0;
    }

    & + #{$root}-form-check-label {
      position: relative;
      cursor: pointer;
      user-select: none;

      &:before {
        content: '';
        background: #fff;
        position: absolute;
        top: 1px;
        left: 0;
        width: 16px;
        height: 16px;
        border: 1px solid var(--form-control-border-color);
        box-sizing: border-box;
        transition: border-color .3s ease, background-color .3s ease, box-shadow .3s ease;
        z-index: 1;
      }
    }

    &:not(#{&}--error):hover + #{$root}-form-check-label {
      &:before {
        border-color: var(--form-control-hover-border-color);
      }
    }

    &:focus + #{$root}-form-check-label {
      &:before {
        border-color: var(--secondary-color);
        box-shadow: 0px 0px 0px 1px var(--secondary-color);
      }
    }

    &:disabled + #{$root}-form-check-label {
      cursor: not-allowed;

      &:before {
        background: var(--form-check-input-disabled-bg-color);
      }
    }

    &[type="checkbox"] + #{$root}-form-check-label {
      &:before {
        border-radius: var(--default-border-radius);
      }
    }

    &[type="checkbox"]:checked + #{$root}-form-check-label {
      &:before {
        @include base-icon;
        content: $wpfs-icon-check;
        background: var(--secondary-color);
        color: #fff;
        font-size: 9px;
        border-color: var(--secondary-color);
        text-align: center;
        padding-top: 3px;
      }
    }

    &[type="radio"] + #{$root}-form-check-label {
      &:before {
        border-radius: 50%;
      }

      &:after {
        content: '';
        background: var(--secondary-color);
        position: absolute;
        top: 5px;
        left: 4px;
        width: 8px;
        height: 8px;
        z-index: 2;
        border-radius: 50%;
        opacity: 0;
        transition: opacity .3s ease;
      }
    }

    &[type="radio"]:checked + #{$root}-form-check-label {
      &:before {
        border-color: var(--secondary-color);
      }

      &:after {
        opacity: 1;
      }
    }

    &--error + #{$root}-form-check-label {
      &:before {
        border-color: var(--error-color);
      }
    }
  }

  &-form-check-label {
    font-size: 15px;
    padding-left: spacer(3);
  }

  &-form-select {
    height: 40px;

    select {
      background: var(--form-control-bg-color);
      width: 100%;
      height: 40px;
      font-family: var(--font-family);
      font-size: 15px;
      padding: 0 spacer(2);
      border: 1px solid var(--form-control-border-color);
      border-radius: var(--default-border-radius);
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
    }
  }

  &-form-actions {
    display: flex;
    align-items: center;
    margin-top: spacer(4);
  }

  &-form-label-info {
    position: absolute;
    top: 0;
    right: -13px;
    color: inherit;
    font-size: 12px;
    cursor: pointer;
  }

  &-form-row {
    display: flex;
    flex-wrap: wrap;
    margin-left: spacer(-1);
    margin-right: spacer(-1);
  }

  &-form-col {
    flex-basis: 0;
    flex-grow: 1;
    max-width: 100%;
    padding-right: spacer(1);
    padding-left: spacer(1);

    @include bp-down(xs) {
      min-width: 100%;
    }
  }

  &-form-error-message {
    color: var(--error-color);
    font-size: 13px;
    font-weight: bold;
    margin-top: spacer(0.5);
  }

  &-form-message {
    color: var(--form-message-font-color);
    font-size: 15px;
    line-height: 24px;
    padding: spacer(1) spacer(2);
    border-radius: var(--default-border-radius);
    margin-bottom: spacer(3);

    &--correct,
    &--incorrect {
      position: relative;
      padding-left: spacer(8);

      &:before {
        @include base-icon;
        position: absolute;
        top: 16px;
        left: spacer(2);
        color: var(--form-message-font-color);
        font-size: 30px;
      }
    }

    &--correct {
      background: var(--success-color);

      &:before {
        content: $wpfs-icon-check-circle;
      }
    }

    &--incorrect {
      background: var(--error-color);

      &:before {
        content: $wpfs-icon-error-diamond;
      }
    }

    &--sm-icon {
      padding: spacer(1.5) spacer(2) spacer(1.5) spacer(7);

      &:before {
        font-size: 22px;
        margin-top: -3px;
      }
    }

    a {
      color: var(--form-message-font-color);
      font-weight: bold;
      text-decoration: none;
    }
  }

  &-form-message-title {
    color: var(--form-message-font-color);
    line-height: 24px;
    font-weight: bold;
  }

  &-form-thank-you {
    padding: spacer(5);
    text-align: center;
  }

  &-form-thank-you-icon {
    color: var(--success-color);
    font-size: 64px;
    margin-bottom: spacer(3);
  }

  &-form-thank-you-title {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: spacer(2);
  }

  &-form-thank-you-message {
    color: var(--form-thank-you-message-font-color);
    font-size: 15px;
    line-height: 24px;
    margin-bottom: spacer(3);

    a {
      color: var(--primary-color);
      text-decoration: none;
    }
  }

  &-form-label,
  &-form-check-label {
    a:not(#{$root}-btn) {
      color: var(--link-font-color);
      transition: all .3s ease;
      border-bottom: 1px solid transparent;
      text-decoration: none;

      &:hover,
      &:active {
        color: var(--link-hover-font-color);
        border-bottom-color: var(--link-hover-font-color);
      }
    }
  }
}
