// =============================================================================
// jQuery UI
// =============================================================================

.#{$wpfs}-ui {
  &.ui-front,
  .ui-front {
    z-index: 10000;
  }

  &.ui-menu,
  .ui-menu {
    list-style: none;
    padding: 0;
    margin: 0;
    display: block;
    outline: none;
  }

  &.ui-widget-content,
  .ui-widget-content {
    background: var(--ui-widget-content-bg-color);
    border: 1px solid var(--form-base-color);
    border-radius: var(--default-border-radius);
  }

  .ui-menu-item {
    cursor: pointer;
    margin: 0;
  }

  .ui-button {
    position: relative;
    display: inline-block;
    cursor: pointer;
    user-select: none;
    color: var(--primary-color);
    font-weight: normal;
  }
}

.ui-helper-hidden-accessible {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}
