// =============================================================================
// Spacing
// =============================================================================

.#{$wpfs} {
  @for $i from 0 through 5 {
    &-mt-#{$i},
    &-my-#{$i} {
      @if $i == 0 {
        margin-top: 0;
      } @else {
        margin-top: spacer($i);
      }
    }

    &-mb-#{$i},
    &-my-#{$i} {
      @if $i == 0 {
        margin-bottom: 0;
      } @else {
        margin-bottom: spacer($i);
      }
    }

    &-ml-#{$i},
    &-mx-#{$i} {
      @if $i == 0 {
        margin-left: 0;
      } @else {
        margin-left: spacer($i);
      }
    }

    &-mr-#{$i},
    &-mx-#{$i} {
      @if $i == 0 {
        margin-right: 0;
      } @else {
        margin-right: spacer($i);
      }
    }
  }
}
