// =============================================================================
// Input groups
// =============================================================================

.#{$wpfs} {
  $root: &;

  &-input-group {
    display: flex;
    align-items: stretch;
    border: 1px solid var(--form-control-border-color);
    border-radius: var(--default-border-radius);
    transition: all .3s ease;

    &:not(#{&}--disabled):not(#{&}--error):not(:focus-within):hover {
      border: 1px solid var(--form-control-hover-border-color);
    }

    &:not(#{&}--error):focus-within {
      border-color: var(--secondary-color);
      box-shadow: 0px 0px 0px 1px var(--secondary-color);
    }

    &--error {
      border-color: color('mordant-red');
    }

    & > #{$root}-input-group-form-control {
      flex: 1 1 auto;
      width: 1%;
    }

    & > #{$root}-input-group-form-control:not(:first-child) {
      padding-left: 0;
      border: 0;
    }

    & > #{$root}-input-group-form-control:not(:last-child) {
      padding-right: 0;
      border: 0;
    }
  }

  &-input-group-prepend,
  &-input-group-append {
    background: #fff;
    display: flex;
    align-items: center;
    height: 40px;
    padding: 0 spacer(2);
    border-radius: var(--default-border-radius);
    cursor: text;
  }

  &-input-group-prepend {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    border-right: 0;
  }

  &-input-group-append {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    border-left: 0;
  }

  &-input-group-text {
    color: var(--form-base-color);
    font-size: 13px;
    font-weight: bold;
  }

  &-input-group-link {
    color: var(--secondary-color);
    font-size: 15px;
    font-weight: bold;
    text-decoration: none;
    transition: color .3s ease;

    &:focus {
      outline: none;
    }

    &:focus,
    &:hover {
      color: var(--link-hover-font-color);
    }

    &--loader {
      position: relative;
      width: 24px;

      span {
        display: none;
      }

      &:after {
        @include base-icon;
        content: $wpfs-icon-spinner;
        position: absolute;
        top: 50%;
        left: 0;
        font-size: 24px;
        margin-top: -12px;
        transform-origin: 50% 52%;
        animation: loader 1s infinite ease;
      }
    }
  }

  &-input-group-icon {
    [class^="#{$wpfs}-icon-"],
    [class*=" #{$wpfs}-icon-"] {
      color: var(--form-base-color);
    }
  }

  &-input-group-form-control {
    background: var(--form-control-bg-color);
    width: 100%;
    height: 38px;
    font-size: 15px;
    padding: 0 spacer(2);
    border: 0;
    border-radius: var(--default-border-radius);

    &:focus {
      outline: none;
    }

    &::placeholder {
      color: var(--form-control-placeholder-color);
      font-family: var(--font-family);
      transition: color .3s ease;
    }
  }
}
