// =============================================================================
// Button check group
// =============================================================================

.#{$wpfs} {
  $root: &;

  &-button-group {
    display: block;
    max-width: 480px;
    min-width: 0;
    margin-bottom: 0;

    #{$root}-form-check-group-title {
      margin-bottom: spacer(1);
    }

    #{$root}-form-check-input {
      &:checked + #{$root}-btn-outline-primary {
        background: var(--primary-bg-color);
        color: #fff;
        border-color: var(--primary-bg-color);
      }

      &:disabled + #{$root}-btn-outline-primary {
        border-color: var(--outline-primary-disabled-border-color);
        color: var(--outline-primary-disabled-font-color);
        box-shadow: none;
        cursor: not-allowed;
      }
    }

    &--without-space {
      #{$root}-button-group-row {
        margin-left: 0;

        & > #{$root}-button-group-item:not(:first-child) {
          #{$root}-btn {
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
          }
        }

        & > #{$root}-button-group-item:not(:last-child) {
          #{$root}-btn {
            border-top-right-radius: 0;
            border-bottom-right-radius: 0;
          }
        }
      }

      #{$root}-button-group-item {
        padding-left: 0;

        &:not(:last-child) {
          #{$root}-btn-outline-primary {
            border-right: 0;
          }
        }

        &:hover {
          & + #{$root}-button-group-item {
            #{$root}-btn-outline-primary  {
              border-left-color: var(--outline-primary-hover-focus-border-color);
            }
          }
        }

        #{$root}-btn {
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
      }
    }
  }

  &-button-group-row {
    display: flex;
    margin-left: spacer(-2);

    &--fixed {
      flex-wrap: wrap;

      #{$root}-button-group-item {
        flex: none;
        max-width: none;
        min-width: auto;

        #{$root}-btn {
          padding-left: spacer(1.5);
          padding-right: spacer(1.5);
        }
      }
    }

    @include bp-down(xs) {
      margin-left: spacer(-1);
    }
  }

  &-button-group-item {
    flex: 1;
    position: relative;
    max-width: 100%;
    min-width: 0;
    padding-left: spacer(2);
    margin-bottom: spacer(2);

    @include bp-down(xs) {
      padding-left: spacer(1);
    }

    #{$root}-btn {
      display: block;
      width: 100%;
      padding-left: spacer(2);
      padding-right: spacer(2);

      @include bp-down(xs) {
        padding-left: spacer(1);
        padding-right: spacer(1);
      }
    }
  }
}
