// =============================================================================
// Functions
// =============================================================================

// Color
@function color($key) {
  @if map-has-key($colors, $key) {
    @return map-get($colors, $key);
  }

  @warn 'Unknown `#{$key}` in $colors.';
  @return null;
}

// Spacer
@function spacer($multiplier) {
  @return $multiplier * $default-spacer;
}
