// =============================================================================
// Stepper
// =============================================================================

.#{$wpfs} {
  $root: &;

  &-stepper {
    .ui-spinner {
      position: relative;
      display: inline-block;
    }

    .ui-spinner-input {
      padding: 0 40px;
      text-align: center;
    }

    .ui-spinner-button {
      position: absolute;
      top: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 40px;
      height: 100%;
      cursor: pointer;
      color: transparent;
      text-align: center;
      box-sizing: border-box;

      &:not(.ui-state-disabled):hover {
        .ui-icon {
          color: var(--secondary-color);
        }
      }

      &.ui-state-disabled {
        cursor: not-allowed !important;
        outline: none;
        opacity: 1;
      }

      .ui-icon {
        color: var(--form-base-color);
        transition: color .3s ease;
      }
    }

    .ui-spinner-down {
      left: 0;
    }

    .ui-spinner-up {
      right: 0;
    }

    .ui-button-icon-space {
      display: none;
    }
  }
}
