// =============================================================================
// Datepicker
// =============================================================================

.#{$wpfs} {
  $root: &;

  &-datepicker {
    #{$root}-input-group-icon {
      font-size: 24px;
    }
  }

  &-datepicker-div {
    width: 320px;
    font-size: 15px;
    padding: spacer(3) spacer(2);
    margin-left: -2px;

    &.top {
      margin-top: spacer(0.5);
    }

    &.bottom {
      margin-top: spacer(-0.5);
    }

    .ui-datepicker-header {
      position: relative;
      margin-bottom: spacer(3);
    }

    .ui-datepicker-prev,
    .ui-datepicker-next {
      position: absolute;
      top: 0;
      cursor: pointer;

      .ui-icon {
        @include base-icon;
        display: inline-block;
        color: var(--form-base-color);
        font-size: 12px;

        &:after {
          content: $wpfs-icon-arrow;
        }
      }
    }

    .ui-datepicker-prev {
      left: 0;

      .ui-icon {
        transform: rotate(90deg);
      }
    }

    .ui-datepicker-next {
      right: 0;

      .ui-icon {
        transform: rotate(-90deg);
      }
    }

    .ui-datepicker-title {
      text-align: center;
    }

    .ui-datepicker-calendar {
      width: 100%;
      table-layout: fixed;
      border-collapse: collapse;
      border: 0;

      th {
        width: 32px;
        height: 32px;
        color: var(--form-base-color);
        font-size: 13px;
        vertical-align: middle;
      }

      td {
        text-align: center;
        font-size: 15px;
        padding: spacer(0.5);
        border: 0;

        &:not(.ui-datepicker-unselectable) {
          .ui-state-default:not(.ui-state-active) {
            &:hover {
              background: var(--ui-state-active-bg-color);
            }
          }
        }

        &.ui-state-disabled,
        &.ui-datepicker-other-month {
          .ui-state-default {
            color: var(--form-secondary-color);
          }
        }
      }

      .ui-state-default {
        display: block;
        width: 32px;
        height: 32px;
        line-height: 32px;
        color: var(--primary-color);
        text-decoration: none;
        border-radius: var(--default-border-radius);
        transition: background .3s ease, color .3s ease;
      }

      .ui-state-active {
        background: var(--secondary-color);
        color: var(--ui-state-active-font-color);
      }
    }
  }
}
